import React, { useState } from "react";
import "./Menu.css";
const playBtn = require("../../Assets/botao_play_redondo.svg");
const banner = require("../../Assets/banner.png");

export default function Menu({ startInfographic, setName, name }) {
  const [moveContainer, setMoveContainer] = useState(false);

  const handleYes = () => {
    setMoveContainer(true);
  };

  return (
    <div
      className={moveContainer ? "menu-content menu-animation" : "menu-content"}
      onAnimationEnd={() => startInfographic(1)}
    >
      <p
        style={{
          textAlign: "center",
          marginHorizontal: 16,
          marginTop: 16,
          color: "#aaa"
        }}
      >
        SAMUEL E OS SAMUELITOS APRESENTAM
      </p>
      <img src={banner} width="100%" />
      <h2 className="title px-4 my-3">Onde devo monetizar meus vídeos ?</h2>
      <p className=" px-4">
        Já ouviu falar em 💰 <b>monetização</b> 💰? Derivado do verbo monetizar,
        o seu significado está ligado a transformação de qualquer coisa em fonte
        de renda. Seja um objeto, uma ideia, um site ou algo que está presente
        no dia a dia de tantas pessoas, a monetização permite criar ilimitadas
        oportunidades para ganhar dinheiro.
      </p>
      <p className=" px-4">
        Se você quer ser um <b>produtor</b> de conteúdo e não sabe qual
        plataforma é o melhor lugar para você? Quem sabe nós podemos te ajudar a
        decidir :)
        <br /> Insira seu nome abaixo, aperte o play e vem com a gente descobrir
        a melhor casa das suas produções audiovisuais.
      </p>
      <div className="name-container-wrapper">
        <div className="name-container">
          <input
            className="insert-name-input"
            value={name}
            onChange={e => setName(e.target.value)}
            type="input"
            placeholder="Digite seu nome"
          />
          <button onClick={() => handleYes()} type="button">
            <img
              className="play-button"
              alt="Botão de inicio da interação"
              src={playBtn}
            />
          </button>
        </div>
      </div>
    </div>
  );
}
